.tools {
	margin: 4px;
}
.tools-open > button {
	-webkit-transform: scale(1.2) rotate(45deg);
	    -ms-transform: scale(1.2) rotate(45deg);
	        transform: scale(1.2) rotate(45deg);
}
.tools-ext-close {
	width: 100%;
	-webkit-transform: translate(0px, -335px);
	    -ms-transform: translate(0px, -335px);
	        transform: translate(0px, -335px);
	-webkit-transition: -webkit-transform 450ms;
	transition: -webkit-transform 450ms;
	-o-transition: transform 450ms;
	transition: transform 450ms;
	transition: transform 450ms, -webkit-transform 450ms

}
.tools-ext-open {
	position: absolute;
	width: 100%;
	-webkit-transform: translate(0px, 0px);
	    -ms-transform: translate(0px, 0px);
	        transform: translate(0px, 0px);
	-webkit-transition: width .5s, -webkit-transform 450ms;
	transition: width .5s, -webkit-transform 450ms;
	-o-transition: transform 450ms, width .5s;
	transition: transform 450ms, width .5s;
	transition: transform 450ms, width .5s, -webkit-transform 450ms;
}



